// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// import 'lib-flexible/index'
import 'lib-flexible'
import Router from 'vue-router'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import toast from 'index/components/Toast'
import Area from 'vant/lib/area';
import 'vant/lib/area/style';
import { Picker } from 'vant';
import Calendar from 'vue-mobile-calendar'
import MUtil from '@/utils/mm.js'
import { DatetimePicker } from 'vant';

Vue.use(DatetimePicker);
Vue.use(Picker);
Vue.use(Calendar);
Vue.use(Area);
Vue.use(toast);

const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}


Vue.use(VueAwesomeSwiper)
Vue.config.productionTip = false

//
// Vue.prototype.$tips=Tips;





//
Vue.prototype.prev = function () {
  this.$router.go(-1)
}
//iOS
Vue.prototype.fixScroll = function () {
  window.scrollTo(0, 0);
}



router.beforeEach((to, from, next) => {
  let statusString = to.query.statusString;
  let jumpTo = to.query.jumpTo;
  let head = to.query.head;
  let nickName = to.query.nickName;
  let inviteCode = to.query.inviteCode;
  console.log('statusString',statusString)
  console.log('jumpTo',jumpTo)
  console.log('head',head)
  console.log('nickName',nickName)
  //urlStatusString jumpTo loginInfoBindPhone   
  if(statusString && statusString != '' && jumpTo === 'loginInfoBindPhone' && head && head != '' && nickName && nickName != ''){
    if (to.path === '/loginInfo') { //
      next()
    } else {
      next({
        path: '/loginInfo',
        query: {
          'statusString':statusString,
          'jumpTo':jumpTo,
          'head':head,
          'nickName':nickName,
          'type':'bindPhone'
        }
      })
    }
  }else if(inviteCode && inviteCode != ''){
    if (to.path === '/register') { //
      next()
    } else {
      next({
        path: '/register',
        query:{
          'inviteCode':inviteCode
        }
      })
    }
  }
  else if(jumpTo === 'home'){   //
    if (to.path === '/') { //
      next()
    } else {
      next({
        path: '/'
      })
    }
  }else {
    if (to.matched.some(record => record.meta.authLogin)) {  // 
      MUtil.request('/user/isLogin', {}, 'get')
        .then(res => {
          console.log('', res)
          if (res.data === '') {
            next();
          } else {
            next({
              path: '/login',
              query: {redirect: to.fullPath}  // path
            })
          }
        });
    } else {
      next();
    }
  }
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



