<template>
  <!---->
  <div>
    <div class="mask"></div>
    <div class="posterBox">

      <div id="posterHtml" ref="posterHtml" v-show="showPosterImg">
        <div class="posterCon">
          <div class="posterItem"> {{phone}} </div>
          <div class="posterItem"> 5 </div>
        </div>
        <!--  -->
        <div class="qrcode"><div id="qrcodeImg"></div></div>
      </div>
      <div class="posterImgCon">
        <img class="posterImg" :src="posterImg" alt="">
      </div>
      <div class="posterTips">
        
      </div>
    </div>
  </div>
</template>

<script>
  import MineInfo from 'index/service/mine-service.js'
  import QRCode from 'qrcodejs2'
  import html2canvas from 'html2canvas'
    export default {
      name: "InvitePoster",
      data(){
        return{
          phone:'',
          showPosterImg:true,
          posterImg: '', // 
        }
      },
      mounted(){
        this.getUserPhone();
      },
      methods:{
        //
        getUserPhone(){
          MineInfo.getUserInfo()
            .then(res => {
              console.log('res',res)
              if(res.code === '0'){
                this.phone = res.data.tel_number
                this.inviteLink(); //
              }
            });
        },
        //
        inviteLink(){
          MineInfo.inviteLink()
            .then(res => {
              console.log('res',res);
              if(res.code == 0){
                let url = res.data;
                this.createQrcode(url);
              }
            })
        },
        createQrcode(text) {
          // 
          const qrcodeImgEl = document.getElementById('qrcodeImg')
          qrcodeImgEl.innerHTML = ''
          const width = parseInt(window.getComputedStyle(qrcodeImgEl).width)
          const height = parseInt(window.getComputedStyle(qrcodeImgEl).height)
          let qrcode = new QRCode(qrcodeImgEl, {
            width: width,
            height: height,
            colorDark: '#000000',
            colorLight: '#ffffff',
            correctLevel: QRCode.CorrectLevel.H
          })
          qrcode.makeCode(text)
          this.createPoster();
        },

        createPoster() {
          // 
          //  canvas 
          const canvas = document.createElement("canvas")
          console.log('canvas',canvas);
          //  DOM 
          let canvasBox = this.$refs.posterHtml
          // 
          const width = parseInt(window.getComputedStyle(canvasBox).width)
          const height = parseInt(window.getComputedStyle(canvasBox).height)

          console.log('width',width);
          console.log('height',height);
          canvas.width = width;
          canvas.height = height;
          canvas.style.width = width + 'px';
          canvas.style.height = height + 'px';

          console.log('canvaswidth',canvas.width);
          console.log('canvasheight',canvas.height);

          const context = canvas.getContext("2d");
          context.scale(1, 1);

          const options = {
            backgroundColor: null,
            canvas: canvas,
            useCORS: true
          }
          html2canvas(canvasBox, options).then((canvas) => {
            // toDataURL  base64
            let dataURL = canvas.toDataURL("image/png")
            this.posterImg = dataURL;
            // console.log('dataURL',dataURL)
          })
        }
      }
    }
</script>

<style lang="less" scoped>
  .mask{
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .4;
    z-index: 99;
    position: absolute;
    top: 0;
    left: 0;
  }
  .posterBox{
    width: 280 * 2px;
    height: 540 * 2px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background: #fff;
    border: 10px solid #fff;
    z-index: 99;
  }
  #posterHtml{
    width: 280 * 2px;
    height: 500 * 2px;
    z-index: 999;
    background:url("~index/assets/poster/invitePoster.png") no-repeat;
    background-size:280*2px 500*2px;
    position: absolute;
  }
  .posterTips{
    width: 100%;
    height: 65px;
    position: absolute;
    bottom: 0;
    color: #999;
    font-size: 30px;
    text-align: center;
    line-height: 65px;
  }
  .posterImgCon{
    width: 280 * 2px;
    height: 500 * 2px;
    z-index: 9999;
    position: absolute;
    /*top: 0;*/
    /*left: 0;*/
    /*right: 0;*/
    /*bottom: 0;*/
    /*margin: auto;*/
  }
  .posterImg{
    width: 100%;
    height: 100%;
  }
  .qrcode{
    width: 160px;
    height: 160px;
    /*background: orange;*/
    margin-top: 225px;
    margin-left: 72px;
  }
  #qrcodeImg{
    width: 100%;
    height: 100%;
  }
  .posterCon{
    width: 440px;
    height: 120px;
    margin: auto;
    margin-top: 450px;
  }
  .posterItem{
    width: 100%;
    height: 50px;
    line-height: 50px;
    color: #ECD8A5;
    font-size: 30px;
  }
</style>



