import MUtil from '@/utils/mm.js'

const shop = {
  //
  getRecommandGoods(){
    return MUtil.request('/good/getRecommendGood',{},'get')
  },
  //
  getGoodsList(data){
    return MUtil.request('/good/getManyGoods',data)
  },
  //
  makeOrder(data){
    return MUtil.request('/order/makeOrder',data)
  }

};


export default shop



